import echarts from "../../node_modules/echarts"
import "echarts/map/js/china.js" //引入中国地图
import "echarts/map/js/world.js"


	//	console.log('data1',option.series[0].data);
	
export default {
	props: {
	    selectedDepartment: Number
	}, 
	data() {
		return { 
			showOptions: false,
			itemList:[{name:"国内",id:'inland'},{name:"世界",id:'global'}],
			itemSelected: 'inland',
			currentConfirmedCount: 1947236,
		//	currentConfirmedCount: '',
			confirmedCount: 3070127,
			curedCount: 905341,
			deadCount: 217550,
			currentConfirmedIncr: 2576,
			confirmedIncr: 18709,
			curedIncr: 15835,
			deadIncr: 298,
			numList:'',
			mapList:'',
			myChart:'',
			mapData:this.getMapData()
		}
	},
	created(){
		this.itemSelected = this.itemList[0].id;
	},
	mounted(){  //template挂载到页面时调用
		this.getMapData();
		this.myChart = echarts.init(document.getElementById('map'));
		
	    this.getOutlineData(); //执行getOutlineData方法
	//	this.drawMap(this.mapData);
	},
	methods: {
		getMapData(){
			uni.request({
				url: this.$serverUrl + '/getMapData?area=' + this.itemSelected,
				//method: 'GET',
				success: (ret) => {
					if (ret.statusCode !== 200) {
						console.log('失败!');
					}
					console.log('ret',ret);
					this.mapList=ret.data.newslist;
					console.log('mapList',this.mapList);
			
					for (var item in this.mapList) { 
						console.log('item',this.mapList[item]);
						this.mapData[item] = {
									name:this.mapList[item].provinceShortName,
									value:this.mapList[item].confirmedCount
								}
					}
					
					console.log('data',this.mapData);
				}
			});
			return this.mapData;
			this.drawMap(this.mapData);
		},
		drawMap(dataList){
			var map = echarts.init(document.getElementById('map'));
				var COLORS = ["#ffffff", "#faebd2", "#e9a188", "#d56355", "#bb3937", "#772526", "#480f10"];//图例里的颜色
			//	let dataList = this.mapData;
				/*var dataList=[//数据
							    {name:"南海诸岛",value:0},
							    {name: '北京', value: 97},
							    {name: '天津', value: 5},
							    {name: '上海', value: 30},
							    {name: '重庆', value: 2}];*/
				console.log("datalist",dataList);
				console.log("mapData",this.mapData);
			//	var dataList = [];
				var option={//配置项（名称）
								tooltip: {//提示框组件
										formatter:function(params,ticket, callback){//提示框浮层内容格式器，支持字符串模板和回调函数两种形式。
											return params.seriesName+'<br />'+params.name+'：'+params.value
										}//数据格式化
									},
								backgroundColor:'#eeeeee',//背景色
								visualMap: {//visualMap 是视觉映射组件，用于进行『视觉编码』，也就是将数据映射到视觉元素（视觉通道）。
									type: 'piecewise',//分段型视觉映射组件
									orient: 'horizontal',//方向
				
									left: 'left',//位置
									top: 'bottom',//位置
					
									pieces: [{//自定义『分段式视觉映射组件』的每一段的范围，以及每一段的文字，以及每一段的特别的样式。
										value: 0, color: COLORS[0]
									}, {
										min: 1, max: 9, color: COLORS[1]
									}, {
										min: 10, max: 99, color: COLORS[2]
									}, {
										min: 100, max: 499, color: COLORS[3]
									}, {
										min: 500, max: 999, color: COLORS[4]
									}, {
										min: 1000, max: 10000, color: COLORS[5]
									}, {
										min: 10000, color: COLORS[6]
									}],
									inRange: {
										color:COLORS //取值范围的颜色
									},
									
									show:true//图注
								},
								geo: {//地理坐标系组件用于地图的绘制
									map: 'china',
									roam: false,//不开启缩放和平移
									zoom:1.23,//视角缩放比例
									label: {
										normal: {
											show: true,
											fontSize:'10',
											color: 'rgba(0,0,0,0.7)'
										}
									},
									itemStyle: {
										normal:{
											borderColor: 'rgba(0, 0, 0, 0.2)'
										},
										emphasis:{
											areaColor: '#F3B329',//鼠标选择区域颜色
											shadowOffsetX: 0,
											shadowOffsetY: 0,
											shadowBlur: 20,
											borderWidth: 0,
											shadowColor: 'rgba(0, 0, 0, 0.5)'
										}
									}
								},
								series : [//系列列表。（图表）
									{
										name: '信息量',
										type: 'map',//图表类型
										geoIndex: 0,
										data:dataList//图表的数据
									}
								]
							}
							
					map.setOption(option);
		},
		getOutlineData() {
			console.log(this.$serverUrl + '/getOutlineData?area=' + this.itemSelected);
			uni.request({
				url: this.$serverUrl + '/getOutlineData?area=' + this.itemSelected,
				//method: 'GET',
				
				success: (ret) => {
					if (ret.statusCode !== 200) {
						console.log('失败!');
					}
					
					this.numList=ret.data;
					console.log('data', ret.data);
					this.currentConfirmedCount = ret.data.currentConfirmedCount;
					this.confirmedCount = ret.data.confirmedCount;
					this.curedCount = ret.data.curedCount;
					this.deadCount = ret.data.deadCount;
									
					this.currentConfirmedIncr = ret.data.currentConfirmedIncr;
					this.confirmedIncr = ret.data.confirmedIncr;
					this.curedIncr = ret.data.curedIncr;
					this.deadIncr = ret.data.deadIncr;
				}
			});
		},
		
		goCommunity() {
			uni.navigateTo({
				url: '/pages/epidemicMap/community'
			});
		},
		goGraph() {
			uni.navigateTo({
				url: '/pages/epidemicMap/graph'
			});
		},
		getItemSelected(){
		    console.log(this.itemSelected)
			this.getOutlineData();
		}
	}
	
}

